<template>
	<div class="register">
		<div class="register-top">
		   <img src="../assets/img/snlogo.png">
		   <div></div>
		   <a href="/login"> ></a>
		</div>
		<div class="register-content">
			<div class="register-form">
				<form action="" method="post">
					<p><span style="color: #f60">199</span></p>
					<p>
						<label></label>
						<input type="text" name="number" v-model="reg.num" maxlength="11" class="auth" placeholder="">
						<span class="error"></span>
						<span class="correct"></span>
						<span class="reajax">!</span>
					</p>
					<p>
						<label></label>
						<input type="text" name="authcode"  placeholder="">
						<a href=""></a>
					</p>
					<p>
						<label></label>
						<input type="password" name="password" v-model="reg.pass" class="auth" placeholder="">
						<span class="error">8</span>
						<span class="correct"></span>
					</p>
					<p>
						<input type="checkbox" class="auth" name="checkbox">
						<a href=""></a>
						<span class="error">!!!!!!!!</span>
					</p>
					<p>
						<input type="button" v-on:click.prevent="post" value="">
					</p>
					<p><a href=""> ></a></p>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import RegJs from '../../static/js/register.js'
export default{
	name:"register",
	data(){
		return{
			reg:{
				num:'18452524290',
				pass:'202cb962ac59075b964b07152d234b70'
			}
		}
	},
	methods:{
		post:function(){
			this.$axios.post('http://api.tp5.com/user/register',{
				username:this.reg.num,
				password:this.reg.pass
			}).then(function(data){
				console.log(data.data);
			})
		}
	}
}
</script>
<style src="@/less/register.less" type="less" scoped>

</style>


