<template>
<div class="container">
	<div class="login">
		<div class="login-top col-md-12">
			<div class="col-md-3">
				<a href="/">
					<img src="../assets/img/snlogo.png">
				</a>
			</div>
		   <div class="col-md-9">
			   	<p>61</p>
			   	<span class="write">
			   		<span class="glyphicon glyphicon-edit"></span><a href=""></a>
			   	</span>
		   </div>
		</div>
		<div class="login-content col-md-12">
			<img class="lg-bg" src="../assets/img/bg-2.jpg">
			<a href=""></a>
			<div class="loginForm">
				<form action="" method="post">
					<p>
						<a href="" class="yellow"></a>
						<a href=""></a>
					</p>
					<P>
						<input type="text" name="num" v-model="log.num" placeholder="///">
					</P>					
					<p>
						<input type="password" name="password" v-model="log.pass" placeholder="">
					</p>
					<p>
						<a href=""></a>
						<a href=""></a>
					</p>
					<p>
						<input type="button" v-on:click.prevent="post" class="logto" value="">
					</p>	
					<p>
						<a href="/register"> ></a>
					</p>
					<p>
						    
					</p>
					<div>
						<a href="">QQ | </a>
						<a href=""> | </a>
						<a href=""> | </a>
						<a href=""></a>
					</div>				
				</form>
			</div>
		</div>
	</div>
</div>
</template>
<script>
export default{
	name:"login",
	data(){
		return{
			log:{
				num:'123',
				pass:'123'
			}
		}
	},
	methods:{
		post:function(){
			this.$axios.post('http://api.tp5.com/user/login',{
				username:this.log.num,
				password:this.log.pass
			}).then(function(data){
				console.log(data.data);
			})
		}
	}
}
</script>
<style src="@/less/login.less" type="less" scoped>
 
</style>


